import React, {Component} from 'react';

import './item-status-filter.css';

export default class ItemStatusFilter extends Component {

    state = {
        filter: 'all'
    }

    setActiveFilter = str => {
        this.setState({ filter: str });
    }

  render() {
      return (
          <div className="btn-group">
              <button
                  onClick={() => {
                      this.props.onFilter('');
                      this.setActiveFilter('all');
                  }}
                  type="button"
                  className={this.state.filter === 'all' ? 'btn btn-info' : 'btn btn-outline-secondary'}>All</button>
              <button
                  onClick={() => {
                      this.props.onFilter('active');
                      this.setActiveFilter('active')
                  }}
                  type="button"
                  className={this.state.filter === 'active' ? 'btn btn-info' : 'btn btn-outline-secondary'}>Active</button>
              <button
                  onClick={() => {
                      this.props.onFilter('done');
                      this.setActiveFilter('done');
                  }}
                  type="button"
                  className={this.state.filter === 'done' ? 'btn btn-info' : 'btn btn-outline-secondary'}>Done</button>
          </div>
      );
  }
};
