import AppHeader from "../app-header";
import SearchPanel from "../search-panel";
import ItemStatusFilter from "../item-status-filter";
import TodoList from "../todo-list";
import InputItem from "../input-item";
import React, {Component} from "react";
import { v4 as uuidv4 } from 'uuid';
import './app.css';

export default class App extends Component {

    state = {
        todoData: [
            { label: 'Drink Coffee', important: false, done: false, id: uuidv4() },
            { label: 'Make Awesome App', important: false, done: false,  id: uuidv4() },
            { label: 'Have a lunch', important: false, done: false, id: uuidv4() }
        ],

        searchString: '',
        activeFilter: ''
    }

    onToggleImportant = id => {
        const idx = this.state.todoData.findIndex(el => el.id === id);
        this.setState(({ todoData }) => {
            return {
                todoData: [
                    ...todoData.slice(0, idx),
                    { ...todoData[idx], important: !todoData[idx].important },
                    ...todoData.slice(idx + 1)
                ]
            }
        });
    }

    onToggleDone = id => {
        const idx = this.state.todoData.findIndex(el => el.id === id);
        this.setState(({todoData}) => {
            return {
                todoData: [
                    ...todoData.slice(0, idx),
                    {...todoData[idx], done: !todoData[idx].done },
                    ...todoData.slice(idx + 1)
                ]
            }
        });
    }

    onAdded = label => {
        this.setState((state) => {
            return {
                todoData: [
                    ...state.todoData,
                    { label, id: uuidv4() }
                ]
            }
        });
    }

    onDeleted = (id) => {
        const idx = this.state.todoData.findIndex(el => el.id === id);
        this.setState(({ todoData }) => {
            return {
                todoData: [
                    ...todoData.slice(0, idx),
                    ...todoData.slice(idx + 1)
                ]
            }
        });
    }

    onSearch = (str) => {
        this.setState({
            searchString: str
        });
    }

    onFilter = (filter) => {
        this.setState( { activeFilter: filter });
    }

    render() {
        const { todoData, searchString, activeFilter } = this.state;
        const doneCount = todoData.filter(el => el.done).length;
        const filteredArr = (this.state.todoData
            .filter(el => el.label.toLowerCase().indexOf(searchString.toLowerCase()) === 0))
            .filter(({ done }) => {
                if (activeFilter === '') return true;
                else if (activeFilter === 'active' && !done) return true;
                else if (activeFilter === 'done' && done) return true;
                else return false;
            });

        return (
            <div className="todo-app">
                <AppHeader toDo={ todoData.length - doneCount} done={doneCount} />
                <div className="top-panel">
                    <SearchPanel onSearch={this.onSearch} />
                    <ItemStatusFilter onFilter={this.onFilter} />
                </div>

                <TodoList
                    onToggleDone={ this.onToggleDone }
                    onToggleImportant={ this.onToggleImportant }
                    onDeleted={ this.onDeleted }
                    todos={filteredArr} />
                <InputItem onAdded={ this.onAdded }/>
            </div>
        );
    }
};
