import React, {Component} from 'react';
import './input-item.css'


export default class InputItem extends Component {
    state = {
        str: ''
    }

    onText = ({ target }) => {
        this.setState({
            str: target.value
        });
    }

    onItemAdded = (e) => {
        e.preventDefault();
        this.props.onAdded(this.state.str);
        this.setState({ str: '' });
    }


    render() {
        return (
            <form
                onSubmit={ this.onItemAdded }
                className="input-group mt-3">
                <input
                    onInput={ this.onText }
                    value={this.state.str}
                    type="text" className="form-control mr-3"
                    placeholder="type here to add new note"
                    aria-label="Recipient's username" aria-describedby="button-addon2" />
                    <button
                        className="btn btn-outline-secondary" type="submit" id="button-addon2">Add</button>
            </form>
        );
    }
}